import React, { useState } from 'react'
import opensea from '../assets/png/opensea.png'
import rarible from '../assets/png/rarible.png'
import capa1 from '../assets/png/capa2.png'
import {motion} from 'framer-motion'

const marketPlaceData = [
  {
    title: 'OPENSEA',
    content: `Once an NFT is minted it will appear at The Oubita Collection in the Opensea and Rarible
      marketplace. Anyone who sales a Out of the Flame Club NFT will have a 5% OUbita Fee for the presale.`,
    img: opensea,
  },
  {
    title: 'RARIBLE',
    content: `Once an NFT is minted it will appear at The Oubita Collection in the Opensea and Rarible
      marketplace. Anyone who sales a Out of the Flame Club NFT will have a 5% OUbita Fee for the presale.`,
    img: rarible,
  },

]

const variantsMarketLeft = {
  hiddenL: {
    opacity: 0,
    x: '150px'
  },
  visibleL: {
    opacity: 1,
    x: '0px',
    transition: {duration:2}
  },
  view: {once:true}
}

const variantsMarketRight = {
  hiddenR: {
    opacity: 0,
    x: '-150px'
  },
  visibleR: {
    opacity: 1,
    x: '0px',
    transition: {duration:2}
  },
  view: {once:true}
}

const variantsMarketFooter = {
  hiddenF: {
    opacity: 0,
    y: '-80px'
  },
  visibleF: {
    opacity: 1,
    y: '0px',
    transition: {duration:2}
  },
  view: {once:true}
}

const Marketplace = ({ id }) => {
  const [marketplace, setMarketplace] = useState(marketPlaceData);
  return (
    <div className='marketplace' id={id}>
      <img src={capa1} className="mancha11" alt="" />

      <h1 className='marketplace__title'>MARKETPLACE</h1>
      {marketplace.map((e, i) => {
        return (
          <motion.div className='marketplace__card' key={i}
            variants={i !== 1 ? variantsMarketLeft: variantsMarketRight}
            initial={i !== 1 ? 'hiddenL': 'hiddenR'}
            whileInView= {i !== 1 ? 'visibleL': 'visibleR'}
            viewport={{
              once: (i !== 1 ? variantsMarketLeft: variantsMarketRight).view.once
            }}
          >
            <div className={i !== 1 ? 'marketplace__card__content' : 'marketplace__card__content --order2'}>
              <h1 className='marketplace__card__content__title'>{e.title}</h1>
              <p className='marketplace__card__content__copy'>{e.content}</p>
              <motion.button className='boton --marketplace__button'
                whileHover={{scale:1.1}}
                whileTap={{scale:1}}
              >LEARN MORE</motion.button>
            </div>
            <img className='marketplace__card__figure_img' src={e.img} alt="" />
          </motion.div>
        )
      })}
      <motion.div className='boton --marketplace__footer'
        variants={variantsMarketFooter}
        initial='hiddenF'
        whileInView='visibleF'
        viewport={{once:variantsMarketFooter?.view?.once}}
      >
        There will be a premarket with a lower price 0,15ETH for the First 2,000 sales. Later
        on the set price will be 0,20 ETH.
      </motion.div>
    </div>
  )
}

export default Marketplace